azo de vestido de noche")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center align-items-center mb-5"},[_c('div',{staticClass:"col-lg-12"},[_c('p',[_vm._v("El vestido, prenda fundamental para el uso en ocasiones tanto casuales como formales, en este caso, se hace hincapié en los vestidos de noche, los cuales se reconocen como requisito en normas de etiqueta de vestir, en los eventos de la mencionada jornada. Los vestidos resaltan en mayor medida las curvas de las féminas siempre y cuando guarde coherencia con la elegancia de la prenda, que es su rasgo primordial. Los sistemas de ajuste como cortes y pinzas, amplitudes, además de sistemas de cierre deben coordinar con el diseño presentado y sus proporciones. Los largos de los vestidos de noche merecen atención en el desarrollo del patronaje sobre todo en lo relacionado con las especificaciones en el corte.")]),_c('div',{staticClass:"row justify-content-center align-items-center py-4",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"col-lg-6"},[_c('div',{staticClass:"cajon color-primario p-4 mb-4"},[_c('p',[_vm._v("La finalidad del ejercicio es poder aplicar todo el conocimiento y manejo de conceptos adquiridos hasta el momento, llevando la implementación de los básicos hacia diferentes tipos de prendas.")])])])])]),_c('div',{staticClass:"col-lg-5"},[_c('div',{staticClass:"titulo-sexto color-acento-contenido"},[_c('h5',[_vm._v("Figura 49. ")]),_c('span',[_vm._v("Vestido de noche ")])]),_c('img',{attrs:{"src":require("@/assets/curso/temas/tema3/figura54.png"),"alt":"En la figura se presentan dos fotografías de mujeres luciendo vestidos de gala de color blanco."}})])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('figure',{staticClass:"mb-5",attrs:{"data-aos":"zoom-in-left"}},[_c('div',{staticClass:"video"},[_c('iframe',{attrs:{"width":"560","height":"315","src":"https://www.youtube.com